.Description {
    display: block;
    margin: auto; 
    width: 80%;
    padding: 20px 0;   
    color: black;
    line-height: 2em;
    font-size: 18px;
    color: #002D85;
}

@media (max-width: 500px){
    .Description {
        padding: 0px 20px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;

    }
}


