.Footer{
    /* width: 75%; */
    /* margin-top: 50px; */
    background-color: #40909a;
    position: relative;
    padding: 35px 0;
    margin-top: 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    overflow: none;
    text-align: center;
    font-family: Roboto;
    -ms-flex-align: center;
        align-items: center;
}

.FooterDiv{
    display: block;
    margin: 0 auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.FooterDiv p{
    color: white;
}

.FooterContainer{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    /* height: 300px; */
}

.FooterDiv span {
    margin-left: 5px;
}


