.Container{
    display: block;
    margin: auto;
    position:relative;
    padding: 10px;
    min-height: 100%;
    height: 100vh;
    overflow: none;
    -ms-flex-align: center;
        align-items: center;
    background: url("../../assets/Images/cover.png") center;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    background-color: teal;
}

.Container .Content{
    position: absolute;
    top: 25%;
    margin-top: -20px;
    padding-left: 150px;
    font-weight: 100;
}

.Container .heading {
    display: block;
    /* position: absolute; */
    top: 30%;
    color: white;
    font-family: 'Bariol';
    font-size: 4.5em;
    font-weight: 100;
    letter-spacing: 3px;
    width: 600px;
}

@media screen and (max-width: 500px){
    .Container{
        height: 75vh;
    }

    .Container .Content{
        position: relative;
        display: block;
        top: 0;
        margin: 100px auto;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        padding: 0;
    }
}

.download a{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 20px 30px;
    border: 1px solid #e1a87a;
    font-size: 1em;
    color: white;
    letter-spacing: 1.3px;
    cursor: pointer;
}

.download a:hover{
    background: #e1a87a;
}

a:visited,
a:link,
a:hover,
a:active{
    /* color: white; */
    text-decoration: none;
}

.Section{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 25px 60px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.Section h2{
    font-size: 2em;
    font-weight: 100;
    width: 300px;
    
}

@media screen and (max-width: 500px) {
    .Container .heading {
        padding-top: 50px;
        font-size: 0.80em;
        width: 100%;
    }

    .Container .heading h1{
        margin-bottom: 40px;
    }

    .Section{
        -ms-flex-direction: column;
            flex-direction: column;
        padding: 10px 15px 10px 10px;
    }
    .Section h2{
        margin: 10px;
    }
}

@media screen and (max-width: 380px) {
    .Container .heading {
        font-size: 0.65em; 
    }
    
}

.WorkExperienceRow {
    /* width: 100%; */
    width: calc(100% - 300px);
    padding: 0 75px;
    line-height: 1.4em;
    padding-top: 20px;
}

.TechnologyRow{
    width: calc(100% - 300px);
    padding: 0 75px;
    line-height: 1.4em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.TechnologyRow li{
    font-size: 1.1em;
    line-height: 2.2em;
    margin-left: 30px;
}

@media screen and (max-width: 500px){
    .TechnologyRow{
        width: 100%;
        padding: 10px 0;
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .TechnologyRow ul{
        -webkit-padding-start: 15px;
           -moz-padding-start: 15px;
                padding-inline-start: 15px;
        -webkit-margin-before: -15px;
                margin-block-start: -15px;
    }
}

.Header h1{
    margin: 10px 0 20px 0;
    padding: 15px;
    border-bottom: 1px solid grey;
}

@media (max-width: 500px){

    .WorkExperienceRow {
        padding: 0 5px;
        width: 100%;
    }

    .Header h1 {
        padding: 0;
        padding-bottom: 15px;
        font-size: 26px;
        text-align: center;
    }
}


