.ProjectRow{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    margin: 0 auto;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.ProjectDiv{
    display: block;
    margin: 50px auto;
    width: 90vw;
    text-align: center;
    -ms-flex-align: center;
        align-items: center;
    
}

@media (max-width: 800px){
    .ProjectRow{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .ProjectHeader {
        display: block;
        margin: 0 auto;
    }
    .ProjectDiv {
        margin: 5px auto;
        padding-bottom: 5px;
    }
}


