.Section{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 25px 60px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    position: relative;
}

.Section h2{
    font-size: 2em;
    font-weight: 100;
    width: 300px;
    
}

.EducationRow {
    /* width: 100%; */
    width: calc(100% - 300px);
    padding: 0 75px;
    line-height: 1.4em;
    padding-top: 20px;
}

.Education{
    padding-bottom: 20px;
}

.Education h3{
    overflow-wrap: break-word;
}

@media screen and (max-width: 800px) {
    .Section{
        -ms-flex-direction: column;
            flex-direction: column;
        padding: 10px;
    }
    .Section h2{
        margin: 5px 0;
    }

    .EducationRow {
        padding: 0 ;
        width: 100%;
    }

    .Education{
        padding-bottom: 10px;
    }
}


